import MenuItem from './MenuItem'
export default {
  data () {
    return {
      menus: [{
        name: '基本信息',
        icon: 'business-basic-icon',
        tip: '酒吧名称',
        route: {
          path: '/BasicBusiness',
          query: {
            id: this.$route.query.id
          }
        }
      }, {
        name: '酒吧公告',
        icon: 'business-notice-icon',
        tip: '公告编辑',
        route: {
          path: '/NoticeSetting',
          query: {
            id: this.$route.query.id
          }
        }
      }, {
        name: '霸屏管理',
        icon: 'business-ba-icon',
        tip: '价格、类型、时长等',
        route: {
          path: '/BaPingSetting',
          query: {
            id: this.$route.query.id
          }
        }
      }, {
        name: '背景设置',
        icon: 'business-bg-icon',
        tip: '大屏幕背景',
        route: {
          path: '/BgSetting',
          query: {
            id: this.$route.query.id
          }
        }
      }, {
        name: '广告设置',
        icon: 'business-ad-icon',
        tip: '广告图片设置',
        route: {
          path: '/AdSetting',
          query: {
            id: this.$route.query.id
          }
        }
      }, {
        name: '管理员',
        icon: 'business-manager-icon',
        tip: '管理员编辑',
        route: {
          path: '/Manager',
          query: {
            id: this.$route.query.id
          }
        }
      }],
      horizontalMenus: [{
        name: '帮助说明',
        icon: 'business-help-icon',
        tip: '设置帮助',
        route: {
          path: '/HelpBusiness'
        }
      }]
    }
  },
  components: {
    MenuItem
  },
  methods: {
    route (index) {
      this.$router.push(this.menus[index].route)
    },
    routeSecond (index) {
      this.$router.push(this.horizontalMenus[index].route)
    }
  }
}



